.calc {
	margin-bottom: rem(70);
	.title {
		margin-bottom: rem(40);

		@include min1150 {
			font-size: rem(40);
		}

		@include min540 {
			margin-bottom: rem(24);
			font-size: rem(32);
		}
	}

	.flex {
		
	}

	.block_wrap {
		align-self: flex-start;
		

		&:first-child {
			padding: rem(32) rem(46);
			width: rem(639);

			@include min1150 {
				padding: rem(24);
				width: 100%;
				margin-bottom: rem(32);
			}

			@include min540 {
				margin-bottom: rem(24);

				.flex {
					flex-direction: column;
				}
			}
		}

		&:last-child {
			padding: rem(32) rem(26);
			width: rem(448);

			@include min1150 {
				padding: rem(24);
				width: 100%;
			}

			

			.field {
				margin-bottom: rem(16);
			}
		}

		.title,
		.subtitle {
			font-size: rem(20);
			font-weight: var(--s-medium);
			margin-bottom: rem(24);
			color: var(--c-text);
			display: block;
			color: black;

			@include min540 {
				font-size: rem(18);
				
			}
		}

		.subtitle {
			margin-top: rem(24);
			margin-bottom: 0;

			@include min540 {
				margin-top: rem(12);
			}

			&:last-child {
				margin-left: rem(100);

				@include min540 {
					margin-left: 0;
				}
			}
		}

		.custom-select {
			margin-bottom: rem(16);
		}
	}
}