.contact {
	margin-bottom: rem(166);

	@include min540 {
		margin-bottom: rem(242);
	}

	.container {
		position: relative;
	}

	&__map {
		position: relative;
		border-radius: rem(16);
		overflow: hidden;

		height: rem(520);

		@include min540 {
			height: rem(380);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		.marker {
			position: absolute;
			top: rem(120);
			left: rem(300);
			background: url(../img/check_map.png) no-repeat;
			background-size: 100%;
			width: rem(80);
			height: rem(82);
		}
	}

	.block_wrap {	
		position: absolute;
		width: rem(288);
		left: rem(160);
		bottom: rem(-30);

		@include min {
			width: rem(248);
			left: rem(14);
			bottom: rem(-100);
		}

		@include min540 {
			// width: 100%;
			bottom: rem(-188);

		}

		.title {
			margin-bottom: rem(20);
			font-size: rem(28);
			font-weight: var(--s-medium);
		}

		.item {
			display: flex;
			// flex-wrap: wrap;
			margin-bottom: rem(25);

			&:last-child {
				margin-bottom: 0;
			}

			.icon {
				width: rem(28);
				height: rem(28);
				flex: 0 0 rem(28);
				margin-right: rem(8);
			}

			.text {
				b {
					margin-bottom: rem(8);
					font-size: rem(20);
					font-weight: var(--s-medium);
					color: var(--c-text);
					display: block;

					@include min {
						font-size: rem(18);
					}
				}

				p, a {
					color: var(--c-text);
					font-size: rem(16);

					@include min {
						font-size: rem(14);
					}
				}
			}
		}

	}
}