.stages {
	margin-bottom: rem(112);

	@include min1150 {
		margin-bottom: rem(72);
	}

	@include min768 {
		margin-bottom: rem(64);
	}

	.title {
		margin-bottom: rem(60);

		@include min1150 {
			margin-bottom: rem(50);
		}

		@include min768 {
			margin-bottom: rem(32);
		}
	}

	&__items {


		.item {
			display: flex;
			// flex-wrap: wrap;
			margin-bottom: rem(64);

			@include min1150 {
				margin-bottom: rem(48);
			}

			@include min {
				flex-wrap: wrap;
			}

			&:last-child {
				margin-bottom: 0;
			}

			b {
				max-width: rem(352);
				flex: 0 0 rem(352);
				margin-right: rem(22);

				font-size: rem(24);
				color: var(--c-text);

				@include min {
					font-size: rem(18);
					flex: 0 0 100%;
					max-width: 100%;
					margin-right: 0;
					margin-bottom: rem(24);
				}

				@include min768 {
					margin-bottom: rem(16);
				}
			}

			&__text {
				position: relative;
				// flex-grow: 1;
				span {
					position: absolute;
					top: rem(-80);
					left: 0;

					color: rgba(73,122,240,.15);
					font-size: rem(144);
					font-weight: var(--s-medium);

					@include min {
						left: rem(40);
					}

					@include min768 {
						left: 0;
						top: rem(-85);
					}
				}

				p {
					font-size: rem(16);

					@include min {
						font-size: rem(14);
						padding-left: rem(58);
					}

					@include min768 {
						padding-left: rem(20);
					}
				}
			}
		}
	}
}